@-webkit-keyframes appear {
  0%{ opacity: 0; top: 400px;}
  100%{ opacity: 1;  top:0;}
}
@keyframes appear {
  0%{ opacity: 0; top: 400px;}
  100%{ opacity: 1;  top:0;}
}
@-webkit-keyframes disappear {
  0%{ opacity: 1; top: 0;}
  100%{ opacity: 0;  top:400px;}
}
@keyframes disappear {
  0%{ opacity: 1; top: 0;}
  100%{ opacity: 0;  top:400px;}
}
body{
  width: 100vw;
  margin: 0;
  overflow-x: hidden;
}
.left-panel-main{
  width: 15vw;
  background-color: #444;
  float: left;
  height: 100vh;
  position: fixed;
  display: inline-block;
  text-align: center;
}
.right-border-panel{
  width: 15vw;
  background-color: #444; 
  float: right;
  height: 100vh;
  position: fixed;
  z-index: 99999999;
  display: inline-block;
}
.nav-li{
  color: white;
  text-align: center;
  padding: 10px;
  font-size: 20px;
  font-weight: 800;
  border-bottom: 2px solid white;
}
.nav-li-top{
  border-top: 2px solid white;
}
.nav-container{
}
.nav-li:hover{
  cursor: pointer;
}
.clickable:hover{
  background-color: rgba(255,255,255,.5)
}
.nav-pers,
.nav-prof{
  font-size: 12px;
  font-weight: normal;
  padding: 3px;
  display: none;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}
.nav-pers-container:hover .nav-pers{
  display:block;
}
.nav-prof-container:hover .nav-prof{
  display: block;
}
.content-block-container{
   padding: 20px;
  margin-top: 20vh;
  height: 40vh;
  margin-bottom: 20vh;
  display: inline-block;
  width: 50%;
  margin-right: 15vw;
  margin-left: auto;
  position: relative;
}
.content-container{ 
  visibility: visible;  
  position: absolute;
  
}
.show{
  visibility: visible;
  -webkit-animation-name: appear;
          animation-name: appear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s; 
}
.hide{
  visibility: visible;
  -webkit-animation-name: disappear;
          animation-name: disappear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s; 
}
.middle-panel-main{
  display: inline-block;
  width: 70vw;
  margin-left: 15vw;
  text-align: center;
  background-color: #e8e8e8;
background-image: url("https://www.transparenttextures.com/patterns/light-honeycomb-dark.png");
  background:cover;
  background-repeat: all;
}

.pic-div{
  float: left;  
  height: 200px;
  width: 300px;
  background-size:cover;

}
.pic{
  height: 200px;
  width: 200px;
}
.list-stuff{
  display: inline-block;
}
.content-title{
  font-size: 35px;
  margin-bottom: 5px;
  font-weight: 800;
  color: #444;
  text-decoration: underline;
}
.backcontainer{

  border-top: 10px solid transparent;
  -webkit-border-image: url('./images/borderImage.png') 15%;
       -o-border-image: url('./images/borderImage.png') 15%;
          border-image: url('./images/borderImage.png') 15%;
  border-image-width:75px 0 75px 0;

}
.about-main,
.featured-inner-container{
  border: 1px solid black;
  padding: 20px;
  margin-top: 20vh;
  margin-bottom: 20vh;
  display: inline-block;
  width: 70%;
  margin-left: auto;
  position: relative;

  border: 10px solid transparent;
  -webkit-border-image: url('https://upload.wikimedia.org/wikipedia/commons/thumb/3/3b/Kagome.svg/554px-Kagome.svg.png') 20%;
       -o-border-image: url('https://upload.wikimedia.org/wikipedia/commons/thumb/3/3b/Kagome.svg/554px-Kagome.svg.png') 20%;
          border-image: url('https://upload.wikimedia.org/wikipedia/commons/thumb/3/3b/Kagome.svg/554px-Kagome.svg.png') 20%;
  border-image-width:10px;
  border-radius: 20px;

  background: #eee;
  z-index: 10;
  min-height: 80vh;
}
.portfolio-divider{
  border-bottom: 20px solid transparent;
  -webkit-border-image: url('https://upload.wikimedia.org/wikipedia/commons/thumb/3/3b/Kagome.svg/554px-Kagome.svg.png') 20%;
       -o-border-image: url('https://upload.wikimedia.org/wikipedia/commons/thumb/3/3b/Kagome.svg/554px-Kagome.svg.png') 20%;
          border-image: url('https://upload.wikimedia.org/wikipedia/commons/thumb/3/3b/Kagome.svg/554px-Kagome.svg.png') 20%;
  border-image-width:0 0 20px 0;
  background-color: white;
  z-index: 100;
  position: relative;
}
.about-main{
  margin-top: 0;
}
.featured-left{
  width: 50%;
  float:left;
}
.featured-title{

}
.featured-work-header{
  color:white;
  text-decoration: underline;
  font-size: 50px;
  position: absolute;
  text-align: center;
  width: 70vw;
  z-index: 5;
}
.mecirc{
  background: url('./images/mecirc.png');
  background-size: contain;
  width: 70%;
  margin-left: 15%;
  margin-top: 10%;
  height: 200px;
  background-repeat: no-repeat;
}
#back-container-5{
  border-image-width: 75px 0 0 0;
}

.clearfix::after {
    content: "";
    clear: both;
    display: table;
}



